@import "./variables";

// Bulma Tooltip Overrides
span[data-tooltip] {
    border: none;
}

// Bulma Overrides
.section.is-medium {
    padding: 6em 3em !important;
}

.button {
    border: unset;

    color: $foreground;
    background-color: transparent;
    border: 3px solid #292d32 !important;

    margin: 6px !important;

    transform: translateY(-0em);
    transition: 0.35s;

    &:hover,
    &:focus {
        color: $foreground;
        box-shadow: inset 6.5em 0 0 0 #292d32;
        border-color: #292d32 !important;
        transform: translateY(-0.5em);
    }
}

.content {
    text-align: left;

    font-size: 20px;

    width: 100%;

    h1, h2, h3, h4, h5, h6 {
        color: $foreground;
    }
}

.card {
    color: $foreground;
    background-color: $background;

    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;

    animation: scale-in .7s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

    .media-content {
        font-weight: bold;
    }

    .c-content {
        font-size: 14px;
    }
}

.navbar {
    margin: 8px 16px;
    background-color: $background-alt;

    .navbar-brand .navbar-burger, .navbar-item {
        color: $foreground;
    }

    .navbar-brand, .navbar-menu {
        background-color: $background;
    }

    .navbar-end {
        .navbar-item {
            background: none;
            margin: 0px 24px 0px 24px;
            border-bottom: 3px solid transparent;

            transition: 0.3s ease-in-out;

            &:hover,
            &:focus {
                color: $foreground;
                border-color: $blue;
            }
        }
    }
}

.footer {
    color: $foreground;
    background-color: $background;
    padding: 4em !important;

    p {
        font-size: 16px;
    }
}